<template>
    <v-content>
      <v-container>
        <h1 class="display-1 pa-2">Телефонная книга</h1>
    <v-card>
      <v-card-title class="title bar-shadow">
        Контакты
        <v-spacer></v-spacer>
       <v-avatar id="cat" :size="48" :tile="true"></v-avatar>
        <v-text-field
          v-model="search"
          append-icon="search"
          label="Найти..."
          single-line
          hide-details
        ></v-text-field>
    </v-card-title>
    <v-toolbar color="white" class="non-shadow">
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog">
        <template v-slot:activator="{ on }">
          <v-btn color="#28a745" dark class="mb-2" v-on="on">Добавить</v-btn>
        </template>
        <v-card id="dialog">
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm8 md12>
                    <input ref="fileInput" type="file" style="display: none;" accept="image/*" id="addfiles" @change="sync">
                    <v-btn
                    color="blue-grey"
                    title="Загрузите ваше фото"
                    class="white--text"
                    @click="upload">
                    Upload
                      <v-icon right dark>cloud_upload</v-icon>
                    </v-btn>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field
                  :counter="20"
                  :rules="nameRules"
                  required
                  v-model="editedItem.yourname"
                  label="Имя/Фамилия"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field
                  :counter="15"
                  :rules="phoneRules"
                  required
                  v-model="editedItem.phone"
                  label="Телефон"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field
                  :rules="[rules.required, rules.email]"
                   v-model="editedItem.email"
                   label="Email"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field
                  :rules="companyRules"
                   v-model="editedItem.company" label="Компания"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-3" flat @click="close">Отмена</v-btn>
            <v-btn color="green darken-3" flat @click="save">Сохранить</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
     <v-data-table
    :headers="headers"
    :items="users"
    :search="search"
    class="elevation-3"
  >
    <template v-slot:items="props">
      <td>{{ props.index + 1 }}</td>
      <td class="text-xs-left py-2"><v-avatar><img  :src="props.item.photo" v-if="props.item.photo" alt="Your photo" width="80px" height="80px"></v-avatar></td>
      <td class="text-xs-left">{{ props.item.yourname }}</td>
      <td class="text-xs-left">{{ props.item.phone }}</td>
      <td class="text-xs-left">{{ props.item.email }}</td>
      <td class="text-xs-left">{{ props.item.company }}</td>
      <td class="justify-center mr-5 align-items-center layout px-2">
        <v-icon
          small
          class="mr-2 pt-3"
          @click="editItem(props.item)"
        >
          edit
        </v-icon>
        <v-icon
          small
          class="pt-3"
          @click="deleteItem(props.item)"
        >
          delete
        </v-icon>
      </td>
  </template>
      <v-alert v-slot:no-results :value="true" color="error" icon="warning">
        Ваш поиск "{{ search }}" не дал результатов.
      </v-alert>
    </v-data-table>
  </v-card>

     <!-- <v-btn class="btn-edit" fab small dark color="#28a745">
        <router-link class="add-icon" to="/contact">
            <v-icon color="#fff" dark>add</v-icon>
        </router-link>
      </v-btn> -->
      </v-container>
    </v-content>
</template>

<script>
// name: 'bookHeader',
// import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      search: '',
      dialog: false,
      image: null,
      editedIndex: -1,
      valid: true,
      name: '',
      phoneRules: [
        v => !!v || 'Phone number is required'
      ],
      companyRules: [
        v => !!v || 'Company is required',
        v => (v && v.length <= 15) || 'Name must be less than 15 characters'
      ],
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 20) || 'Name must be less than 20 characters'
      ],
      email: '',
      rules: {
        required: value => !!value || 'Email is Required.',
        counter: value => value.length <= 20 || 'Max 20 characters',
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'Invalid e-mail.'
        }
      },
      headers: [
        {
          text: '№',
          align: 'left',
          sortable: false
        },
        { text: 'Фото', value: 'photo' },
        { text: 'Имя/Фамилия', value: 'yourname' },
        { text: 'Телефон', value: 'phone' },
        { text: 'Email', value: 'email' },
        { text: 'Компания', value: 'company' },
        { text: 'Форматирование', sortable: false }
      ],
      // users: [],
      // usersKeys: [],
      editedItem: {
        id: '',
        photo: '',
        yourname: '',
        phone: '',
        email: '',
        company: ''
      },
      defaultItem: {
        photo: '',
        yourname: '',
        phone: '',
        email: '',
        company: ''
      }
    }
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Новый контакт' : 'Редактировать контакт'
    },
    phbHeaders () {
      return this.$store.getters.headers
    },
    users: {
      get: function () {
        return this.$store.getters.users
      },
      set: function () {}
    }
    // editedItem: {
    //   get: function () {
    //     return this.$store.getters.editedItem
    //   },
    //   set: function () {}
    // },
    // defaultItem: {
    //   get: function () {
    //     return this.$store.getters.defaultItem
    //   },
    //   set: function () {}
    // }
  },
  watch: {
    dialog (val) {
      val || this.close()
    }
  },
  created () {
    this.initialize()
  },
  methods: {
    sync ($event) {
      // event.preventDefault()
      const file = this.defaultItem.photo = event.target.files[0]
      const reader = new FileReader()
      reader.onload = e => {
        this.editedItem.photo = reader.result
      }
      reader.readAsDataURL(file)
      this.image = file
    },
    upload () {
      this.$refs.fileInput.click()
    },
    initialize () {
      this.users = []
      this.db().ref('phonebook/').once('value').then((list) => {
        list.forEach((item) => {
          this.users.push(item.val())
        })
      })
    },
    editItem (item) {
      this.editedIndex = this.users.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.users.indexOf(item)
      var confirmResult = confirm('Вы уверены в том, что хотите удалить этот элемент?') && this.users.splice(index, 1)
      this.db().ref('phonebook/').once('value').then((list) => {
        list.forEach(element => {
          // console.log(element.key)
          // console.log('Поиск id по клику: ' + item.id)
          if (element.key === item.id && confirmResult !== false) {
            // alert('Вы нашли то что искали: ' + element.key + ' |===| ' + item.id)
            // console.log('Ended element: ' + element.key)
            this.db().ref('phonebook/' + element.key).set(null)
            alert('Контакт успешно удален из базы данных')
          }
        })
      })
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save () {
      let thisEditedItem = this.editedItem
      if (this.editedIndex > -1) {
        let obj = Object.assign(this.users[this.editedIndex], this.editedItem)
        console.log(obj)
        console.log(this.editedItem.id)
        this.db().ref('phonebook/').once('value').then((keylist) => {
          keylist.forEach(elem => {
            console.log('Elem key: ' + elem.key + ' | ' + 'EditedItem id: ' + thisEditedItem.id)
            if (elem.key === thisEditedItem.id) {
              let elemItem = this.db().ref('phonebook/' + elem.key)
              elemItem.update({
                'photo': thisEditedItem.photo,
                'yourname': thisEditedItem.yourname,
                'phone': thisEditedItem.phone,
                'email': thisEditedItem.email,
                'company': thisEditedItem.company
              })// console.log(elem.val())
            }
          })
        })
      } else {
        this.users.push(this.editedItem)
        let userKey = this.db().ref('phonebook/').push().key
        this.editedItem.id = userKey
        this.db().ref('phonebook/' + userKey).set(this.editedItem)
        // console.log('Содержимое user key: ' + this.editedItem.id)
      }
      this.close()
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container-app {
  max-width: 1366px;
}
.wrapper-app {
    max-width: 980px;
    margin: auto;
}
h1 {
    margin-top: 2%;
    border-radius: 2px;
    text-align: center;
    font-weight: lighter;
    color: #fff;
    background-color: #28a745;
}
h2 {
    margin-bottom: 0;
    font-size: 1.7rem;
}

#cat {
    margin-right: 15px;
    background-image: url(http://i.wallpic.net/10/aa1e/kot-podglyadyvaet-v-beluyu-dyrku_1680x1050.jpg);
    background-size: cover;
    background-position: center;
}

.btn-edit {
    position: fixed;
    z-index: 1;
    right: 12%;
    bottom: 2%;
}
.add-icon {
  width: 100%;
  line-height: 40px;
}
.fa-user {
  position: absolute;
  top: 30%;
  left: 25%;
  padding-right: 25px;
}
#dialog {
  max-width: 500px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 5px 100vw rgba(0, 0, 0, 0.5);
  }
  .bar-shadow {
    box-shadow: 0 2px 3px 1px rgba(0, 0, 0, 0.2);
  }
  .non-shadow {
     box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  }
  #addfiles {
    /* background: red; */
    border: 1px solid grey;
    color: #28a745;
    margin-top: 15px;
    border-radius: 4px;
  }

  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }

</style>



