<template>
  <section class="card">
    <div class="card-block">
        <h4 class="headline">Добавить контакт</h4>
    </div>
    <div class="user-face-img">
        <label for="user-face" class="fa fa-user-circle face-label" title="Ваше фото" v-if="!img"></label>
        <input type="file" title="Загрузить фото" id="user-face" @click="photo()">
    </div>
    <div class="card-block">
      <div class="input-container">
         <v-text-field
            label="Ваше имя"
            prepend-icon="fa-user"
            v-model="name"
            :counter="10"
            :rules="nameRules"
            required
          ></v-text-field>
          <v-text-field
            label="№ тел:"
            :counter="15"
            prepend-icon="fa-phone"
            type="phone"
            required
          ></v-text-field>
              <v-text-field
                label="Ваш email"
                prepend-icon="fa-envelope"
                v-model="email"
                :rules="[rules.required, rules.email]"
                ></v-text-field>
            <v-text-field
            label="Компания"
            prepend-icon="fa-building"
          ></v-text-field>
          <v-btn small color="#4CAF50" class="caption white--text" @click="submit">отправить</v-btn>
      </div>
      <div class="user-photo"></div>
    </div>
      <div>{{ pathPhoto }}</div>
      <input type="text" name="" id="" v-model="pathPhoto">
        <v-btn color="blue-grey" small class="v-btn-back">
          <router-link class="text-capitalize white--text" to="/"><v-icon class="vicon" dark left>arrow_back</v-icon>Назад</router-link>
        </v-btn>
</section>

</template>

<script>
export default {
  data () {
    return {
      pathPhoto: '',
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters'
      ],
      email: '',
      rules: {
        required: value => !!value || 'Required.',
        counter: value => value.length <= 20 || 'Max 20 characters',
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'Invalid e-mail.'
        }
      },
      dialog: false,
      headers: [
        {
          text: 'Dessert (100g serving)',
          align: 'left',
          sortable: false,
          value: 'name'
        },
        { text: 'Calories', value: 'calories' },
        { text: 'Fat (g)', value: 'fat' },
        { text: 'Carbs (g)', value: 'carbs' },
        { text: 'Protein (g)', value: 'protein' },
        { text: 'Actions', value: 'name', sortable: false }
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0
      }
    }
  },
  methods: {
    photo () {
      this.pathPhoto = event.target.value
    },
    submit () {
      this.$v.$touch()
    },
    initialize () {
      this.desserts = [
        {
          name: 'Frozen Yogurt',
          calories: 159,
          fat: 6.0,
          carbs: 24,
          protein: 4.0
        },
        {
          name: 'Ice cream sandwich',
          calories: 237,
          fat: 9.0,
          carbs: 37,
          protein: 4.3
        },
        {
          name: 'Eclair',
          calories: 262,
          fat: 16.0,
          carbs: 23,
          protein: 6.0
        },
        {
          name: 'Cupcake',
          calories: 305,
          fat: 3.7,
          carbs: 67,
          protein: 4.3
        },
        {
          name: 'Gingerbread',
          calories: 356,
          fat: 16.0,
          carbs: 49,
          protein: 3.9
        },
        {
          name: 'Jelly bean',
          calories: 375,
          fat: 0.0,
          carbs: 94,
          protein: 0.0
        },
        {
          name: 'Lollipop',
          calories: 392,
          fat: 0.2,
          carbs: 98,
          protein: 0
        },
        {
          name: 'Honeycomb',
          calories: 408,
          fat: 3.2,
          carbs: 87,
          protein: 6.5
        },
        {
          name: 'Donut',
          calories: 452,
          fat: 25.0,
          carbs: 51,
          protein: 4.9
        },
        {
          name: 'KitKat',
          calories: 518,
          fat: 26.0,
          carbs: 65,
          protein: 7
        }
      ]
    },
    editItem (item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      const index = this.desserts.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
    },

    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem)
      } else {
        this.desserts.push(this.editedItem)
      }
      this.close()
    }
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    }
  },
  created () {
    this.initialize()
  }
}
// export default {
//   data () {
//     return {
//       search: ''
//     }
//   },
//   computed: {
//     databookHeaders () {
//       return this.$store.getters.headers
//     },
//     databook () {
//       return this.$store.getters.desserts
//     }
//   }
// }
</script>
<style scope>
.card {
  width: 60%;
  margin: 5% auto;
}
  .user-face-img {
    width: 100%;
    height: 200px;
    background-color:grey;
    border: 1px solid grey;
    line-height: 0;
    text-align: center;
    /* font-size: 11em; */
    color: #fff;
}
.face-label {
  display: block;
  margin-top: 2%;
  cursor: pointer;
}

.card-block {
  display: flex;
}
.user-photo {
  width: 30%;
  height: 80%;
  margin-top: 5%;
  margin-right: 5%;
  border: 1px solid grey;
}
.input-container {
  width: 480px;
  margin: 5% 5% 0 5%;
}
.input-group {
  margin-bottom: 20px;
  border: none;
}
.input-group input {
  border-top: none;
  border-right: none;
}
.v-btn-back {
  width: 10%;
  margin:40px 5% 20px;

}
.back-btn-link {
  width: 100%;
}
.vicon {
  font-size: 1.2rem;
  margin-right: 4%;
}
#dialog {
  max-width: 500px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  }
</style>



