import Vue from 'vue'
import Router from 'vue-router'
import Vuetify from 'vuetify'
import bodybook from '@/container/bodybook'
import addContact from '@/container/addContact'
Vue.use(Vuetify)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '',
      name: 'home',
      component: bodybook
    },
    {
      path: '/contact',
      name: 'addContact',
      component: addContact
    }
  ]
  // mode: 'history'
})



