var fb = firebase.initializeApp({
  apiKey: 'AIzaSyAL0iy49xGfJ0iBGFkyBR9t9LKHuLgQ7GA',
  authDomain: 'phonebook-774b3.firebaseapp.com',
  databaseURL: 'https://phonebook-774b3.firebaseio.com',
  projectId: 'phonebook-774b3',
  storageBucket: 'phonebook-774b3.appspot.com',
  messagingSenderId: '845673005921'
})
var database = fb.database()

Vue.mixin({
  methods: {
    db: function () {
      return database
    }
  }
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



